<template>
  <div>
      <div>{{ id }}</div>
      <div>{{ username }}</div>
      <router-link :to="{path:`/user/${username}/ccc`}"></router-link>
      <router-view></router-view>
  </div>
</template>

<style>
    body {

    }
</style>

<script>
    export default {
      props:{
        id:{
          type:String,
          default:''
        }
      },
      data() {
          return {
              username:''
          }
      },
      mounted() {
        this.$nextTick(()=>{
          this.username = this.id;
        })
        
//         pathparams  query 
//         name  path
//        const userId = 123
//        router.push({ name: 'user', params: { userId }}) // -> /user/123
//        router.push({ path: `/user/${userId}` }) // -> /user/123
//         params :
//        router.push({ path: '/user', params: { userId }}) // -> /user
        console.log(this.$route.params)
      },
    }
</script>



