    <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrderDialog"></check-order>
  </div>
</template>

<script>
    import VChooser from '../../components/base/chooser.vue'
    import VCounter from '../../components/base/counter.vue'
    import VSelection from '../../components/base/selection.vue'
    import Dialog from '../../components/base/dialog.vue'
    import VueDatepickerLocal from 'vue-datepicker-local'
    import BankChooser from '../../components/bankChooser.vue'
    import CheckOrder from '../../components/checkDialog.vue'

    export default {
        components:{
          VChooser,VSelection,VCounter,BankChooser,CheckOrder,
          myDialog:Dialog,
          VueDatepickerLocal
        },
        data() {
            return {
              time: new Date(),
              emptyRange: [],
              price:0,
              buyTypes: [
                {
                  label: '',
                  value: 0
                },
                {
                  label: '',
                  value: 1
                },

              ],
              districts: [
                {
                  label: '',
                  value: 0
                },
                {
                  label: '',
                  value: 1
                },
                {
                  label: '',
                  value: 2
                },
                {
                  label: '',
                  value: 3
                },
                {
                  label: '',
                  value: 4
                },

              ],
              isShowPayDialog:false,
              buyNumber:0,
              bankId:null,
              orderId:null,
              buyType:{},
              district:{},
              isShowCheckOrder:false,
              isShowErrDialog:false,
            }
        },
        methods:{
          payClick() {
            this.isShowPayDialog = true
          },
          hidePayDialog() {
            this.isShowPayDialog = false
          },
          onParamChange(attr,value) {
            this[attr] = value;
            this.getPrice();
          },
          getPrice() {
            let reqParams = {
              buyNumber:this.buyNumber,
              buyType:this.buyType.label,
              district:this.district.label,
              validTime:this.emptyRange
            }
            this.$http.post('/api/getPrice',reqParams)
              .then((res) => {
                this.price = res.data.getPrice.amount;

              },(err) => {

              })
          },
          onChangeBanks(bankObj) {
            this.bankId = bankObj.id;
          },
          confirmBuy(){
            let reqParams = {
              buyNumber:this.buyNumber,
              buyType:this.buyType.label,
              district:this.district.label,
              validTime:this.emptyRange,
              bankId:this.bankId
            }
            this.$http.post('/api/createOrder',reqParams)
              .then((res) => {
                this.orderId = res.data.createOrder.orderId
                this.isShowPayDialog = false;
                this.isShowCheckOrder = true;
              },(err) =>{
                this.isShowErrDialog = true
              })
          },
          hideCheckOrderDialog(){
            this.isShowCheckOrder = false;
          },
          hideErrDialog() {
            this.isShowErrDialog = false;
            this.$router.push({path: '/orderList'});
          }
        },
        mounted() {
          this.buyNumber = 1;
          this.buyType = this.buyTypes[0];
          this.district = this.districts[0];
          this.emptyRange = [];
          this.getPrice();
        }
    }
</script>
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    margin-top: 20px;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
</style>



