    <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrderDialog"></check-order>
  </div>
</template>

<script>
    import VSelection from '../../components/base/selection'
    import VCounter from '../../components/base/counter'
    import VMulChooser from '../../components/base/multiplyChooser'
    import Dialog from '../../components/base/dialog.vue'
    import BankChooser from '../../components/bankChooser.vue'
    import CheckOrder from '../../components/checkDialog.vue'
    import _ from 'lodash'
    export default {
        components:{
          VSelection,
          VCounter,
          VMulChooser,
          myDialog:Dialog,
          BankChooser,
          CheckOrder,
        },
        data() {
            return {
              tradeList: [
                {
                  label: '',
                  value: 0
                },
                {
                  label: '',
                  value: 1
                },
                {
                  label: '',
                  value: 2
                },
                {
                  label: '',
                  value: 3
                },
                {
                  label: '',
                  value: 4
                }
              ],
              versionList: [
                {
                  label: '',
                  value: 0
                },
                {
                  label: '',
                  value: 1
                },
                {
                  label: '',
                  value: 2
                },
                {
                  label: '',
                  value: 3
                }
              ],
              price:0,
              buyNumber:20,
              trade:{},
              versions:[],
              validTime:'',
              isShowPayDialog:false,
              bankId:null,
              orderId:null,
              isShowCheckOrder:false,
              isShowErrDialog:false
            }
        },
      methods:{
        onParamChange(attr,val) {
          this[attr] = val
//          console.log(attr,this[attr])
          this.getPrice();
        },
        getPrice() {
          let buyVersionsArray = _.map(this.versions,(item)=>{
            return item.label
          })
          let reqParams = {
            buyNumber:this.buyNumber,
            trade:this.trade.label,
            versions:buyVersionsArray.join(','),
            validTime:this.validTime,
          }
          this.$http.post('/api/getPrice',reqParams)
            .then((res)=>{
//              console.log(res.data.getPrice.amount)
              this.price = res.data.getPrice.amount
            })
        },
        confirmBuy() {
          let buyVersionsArray = _.map(this.versions,(item)=>{
            return item.label
          })
          let reqParams = {
            buyNumber:this.buyNumber,
            trade:this.trade.label,
            versions:buyVersionsArray.join(','),
            validTime:this.validTime,
          }
          this.$http.post('/api/createOrder',reqParams)
            .then((res)=>{
              this.orderId = res.data.createOrder.orderId;
              this.isShowPayDialog = false
              this.isShowCheckOrder = true
            },(err)=>{
              this.isShowErrDialog = true
            })
        },
        hidePayDialog() {
          this.isShowPayDialog = false
        },
        onChangeBanks(bankObj) {
          this.bankId = bankObj.id;
        },
        payClick() {
          this.isShowPayDialog = true
        },
        hideCheckOrderDialog() {
          this.isShowCheckOrder = false;
        },
        hideErrDialog() {
          this.isShowErrDialog = false;
          this.$router.push({path: '/orderList'})
        }
      },
      mounted() {
        this.buyNumber = 20;
        this.trade = this.tradeList[0];
        this.versions = [this.versionList[0]];
        this.validTime = ''
        this.getPrice();
      }
    }
</script>
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    margin-top: 20px;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
</style>



