<template>
  <div class="sales-board">
    <div class="sales-board-intro">
      <h2>2</h2>
      <p>2</p>
    </div>
    <div class="sales-board-form">
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-counter :max="10" :min="5" @on-change="onParamChange('buyNumber',$event)"></v-counter>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-mul-chooser :selections="versionLists" @on-change="onParamChange('versionList',$event)"></v-mul-chooser>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <my-vue-datepicker-local v-model="timeRange" clearable></my-vue-datepicker-local>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          {{ price }} 
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">&nbsp;</div>
        <div class="sales-board-line-right">
          <div class="button" @click="payClick">
            
          </div>
        </div>
      </div>
    </div>
    <div class="sales-board-des">
      <h2>II</h2>
      <p>II1116II</p>
      <br>
      <p>1116II</p>
      <br>
      <p>1.II</p>
      <p>2.</p>
      <p>3.5</p>
      <br>
      <p>II</p>
      <br>
      <p>609875 198II5</p>
      <br>
      <p>20171031</p>
    </div>
    <my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
      <table class="buy-dialog-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{ buyNumber }}</td>
          <td>
            <span v-for="(item,index) in versionList">{{!index ?  item.label :',' + item.label}}</span>
          </td>
          <td>{{ timeRange}}</td>

          <td>{{ price }}</td>
        </tr>
      </table>
      <h3 class="buy-dialog-title"></h3>
      <bank-chooser @on-change="onChangeBanks"></bank-chooser>
      <div class="button buy-dialog-btn" @click="confirmBuy">
        
      </div>
    </my-dialog>
    <my-dialog :is-show="isShowErrDialog" @on-close="hideErrDialog">
      
    </my-dialog>
    <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrderDialog"></check-order>
  </div>
</template>

<style>
    body {

    }
</style>

<script>
    import VCounter from '../../components/base/counter.vue'
    import VMulChooser from '../../components/base/multiplyChooser.vue'
    import VueDatepickerLocal from '../../components/base/datepicker.vue'
    import Dialog from '../../components/base/dialog.vue'
    import BankChooser from '../../components/bankChooser.vue'
    import CheckOrder from '../../components/checkDialog.vue'
    import _ from 'lodash'
    export default {
        components:{
          VCounter,
          VMulChooser,
          MyVueDatepickerLocal:VueDatepickerLocal,
          myDialog:Dialog,
          BankChooser,
          CheckOrder
        },
        data() {
            return {
              time: new Date(),
              timeRange: '',
              price:0,
              versionLists: [
                {
                  label: '',
                  value: 0
                },
                {
                  label: '',
                  value: 1
                },
                {
                  label: '',
                  value: 2
                },
                {
                  label: '',
                  value: 3
                }
              ],
              buyNumber:5,
              versionList:[],
              isShowPayDialog:false,
              bankId:null,
              orderId:null,
              isShowCheckOrder:false,
              isShowErrDialog:false
            }
        },
        methods:{
          onParamChange(name,value) {
            this[name] = value;
            this.getPrice();
          },
          getPrice() {
            let versionListArray = _.map(this.versionList,function (item) {
              return item.label
            })
            let reqParams = {
              buyNumber : this.buyNumber,
              versionList : versionListArray.join(','),
              validTime : this.timeRange,
            }
            console.log(reqParams)
            this.$http.post('/api/getPrice',reqParams)
              .then((res)=>{
//              console.log(res.data.getPrice.amount)
                this.price = res.data.getPrice.amount
              })
          },
          confirmBuy() {
            let versionListArray = _.map(this.versionList,function (item) {
              return item.label
            })
            let reqParams = {
              buyNumber : this.buyNumber,
              versionList : versionListArray.join(','),
              validTime : this.timeRange,
              bankId:this.bankId
            }
            console.log(reqParams)
            this.$http.post('/api/createOrder',reqParams)
              .then((res)=>{
                this.orderId = res.data.createOrder.orderId;
                this.isShowPayDialog = false
                this.isShowCheckOrder = true
              },(err)=>{
                this.isShowErrDialog = true
              })
          },
          payClick() {
            this.isShowPayDialog = true
          },
          hidePayDialog() {
            this.isShowPayDialog = false
          },
          onChangeBanks(bankObj) {
            this.bankId = bankObj.id;
          },
          hideErrDialog() {
            this.isShowErrDialog = false;
            this.$router.push({path: '/orderList'})
          },
          hideCheckOrderDialog() {
            this.isShowCheckOrder = false;
          }
        },
        mounted() {
          this.buyNumber = 5;
          this.versionList = [];
          this.getPrice();
        }

    }
</script>
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    margin-top: 20px;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
  .sales-board-des p {
    line-height: 1.6;
    text-indent: 2em;
  }
</style>



