<template>
    <div>
      <div class="sales-board">
        <div class="sales-board-intro">
          <h2>3</h2>
          <p>III</p>
          <p></p>
        </div>
        <div class="sales-board-form">
          <div class="sales-board-line">
            <div class="sales-board-line-left">
              
            </div>
            <div class="sales-board-line-right">
              <v-counter
                @on-change="onParamChange('buyNumber',$event)"
              ></v-counter>
            </div>
          </div>
          <div class="sales-board-line">
            <div class="sales-board-line-left">
              
            </div>
            <div class="sales-board-line-right">
              <v-selection
                :selections="productTypes"
                @on-change="onParamChange('buyType',$event)"
              ></v-selection>
            </div>
          </div>
          <div class="sales-board-line">
            <div class="sales-board-line-left">
              
            </div>
            <div class="sales-board-line-right">
              <v-chooser
                :selections="periodList"
                @on-change="onParamChange('period',$event)"
              ></v-chooser>
            </div>
          </div>
          <div class="sales-board-line">
            <div class="sales-board-line-left">
              
            </div>
            <div class="sales-board-line-right">
              <v-mul-chooser
                :selections="versionList"
                @on-change="onParamChange('versions',$event)"
              ></v-mul-chooser>
            </div>
          </div>
          <div class="sales-board-line">
            <div class="sales-board-line-left">
              
            </div>
            <div class="sales-board-line-right">
               {{ price }}
            </div>
          </div>
          <div class="sales-board-line">
            <div class="sales-board-line-left">&nbsp;</div>
            <div class="sales-board-line-right">
              <div class="button" @click="payClick">
                
              </div>
            </div>
          </div>
        </div>
        <div class="sales-board-des">
          <h2></h2>
          <p></p>


          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>

          <h3></h3>
          <p></p>
          <br>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
        <my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
          <table class="buy-dialog-table">
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr>
              <td>{{ buyNumber }}</td>
              <td>{{ buyType.label }}</td>
              <td>{{ period.label }}</td>
              <td>
                <span v-for="(item,index) in versions">{{!index ?  item.label :',' + item.label}}</span>
              </td>
              <td>{{ price }}</td>
            </tr>
          </table>
          <h3 class="buy-dialog-title"></h3>
          <bank-chooser @on-change="onChangeBanks"></bank-chooser>
          <div class="button buy-dialog-btn" @click="confirmBuy">
            
          </div>
        </my-dialog>
        <my-dialog :is-show="isShowErrDialog" @on-close="hideErrDialog">
          
        </my-dialog>
        <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrderDialog"></check-order>
      </div>
    </div>
</template>

<script>
    import VSelection from '../../components/base/selection.vue'
    import VChooser from '../../components/base/chooser.vue'
    import VMulChooser from '../../components/base/multiplyChooser.vue'
    import VCounter from '../../components/base/counter.vue'
    import Dialog from '../../components/base/dialog.vue'
    import BankChooser from '../../components/bankChooser.vue'
    import CheckOrder from '../../components/checkDialog.vue'
    import _ from 'lodash'
    export default {
      components: {
        VSelection,VChooser,VMulChooser,VCounter,
        myDialog:Dialog,
        BankChooser,
        CheckOrder
      },
//      created () {
//        this.$http.post('http://localhost:8080/api/getPrice').then((response) => {
//          console.log(response.data)
//        })
//      },
      data() {
          return {
            buyNumber:0,
            buyType:{},
            versions:[],
            period:{},
            productTypes:[
              {
                label: '',
                value: 0
              },
              {
                label: '',
                value: 1
              },
            ],
            periodList: [
              {
                label: '',
                value: 0
              },
              {
                label: '',
                value: 1
              },
              {
                label: '',
                value: 2
              }
            ],
            versionList: [
              {
                label: '3',
                value: 0
              },
              {
                label: '3',
                value: 1
              },
              {
                label: '3',
                value: 2
              }
            ],
            price:0,
            isShowPayDialog:false,
            bankId:null,
            orderId:null,
            isShowCheckOrder:false,
            isShowErrDialog:false
          }
      },
      methods:{
        onParamChange(attr,val) {
          this[attr] = val
//          console.log(attr,this[attr])
          this.getPrice();
        },
        getPrice() {
          let buyVersionsArray = _.map(this.versions,(item)=>{
            return item.value
          })
          let reqParams = {
            buyNumber:this.buyNumber,
            buyType:this.buyType.value,
            versions:buyVersionsArray.join(','),
            period:this.period.value,
          }
          this.$http.post('/api/getPrice',reqParams)
            .then((res)=>{
//              console.log(res.data.getPrice.amount)
              this.price = res.data.getPrice.amount
            })
        },
        hidePayDialog() {
          this.isShowPayDialog = false
        },
        payClick() {
          this.isShowPayDialog = true
        },
        onChangeBanks(bankObj) {
          this.bankId = bankObj.id;
        },
        confirmBuy() {
          let buyVersionsArray = _.map(this.versions,(item)=>{
            return item.value
          })
          let reqParams = {
            buyNumber:this.buyNumber,
            buyType:this.buyType.value,
            versions:buyVersionsArray.join(','),
            period:this.period.value,
            bankId:this.bankId
          }
          this.$http.post('/api/createOrder',reqParams)
            .then((res)=>{
              this.orderId = res.data.createOrder.orderId;
              this.isShowPayDialog = false
              this.isShowCheckOrder = true
            },(err)=>{
              this.isShowErrDialog = true
            })
        },
        hideCheckOrderDialog() {
          this.isShowCheckOrder = false;
        },
        hideErrDialog() {
          this.isShowErrDialog = false;
          this.$router.push({path: '/orderList'})
        }
      },
      mounted() {
          this.buyNumber = 1,
          this.buyType = this.productTypes[0]
          this.versions = [this.versionList[0]]
          this.period = this.periodList[0]
          this.getPrice()
      }

    }
</script>
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    margin-top: 20px;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
</style>



